<template>
    <div class="workMainWindowRoot">
        <div class="workNavContainer">
            <span></span>
        </div>
        <div class="workHeaderContainer">
            <div class="table">
                <div class="row">
                    <div class="cell">
                        <div>:</div>
                    </div>
                    <div class="cell">
                        <input v-model="carNumber" />
                    </div>
                    <div class="cell">
                        <button @click="selectCarNumber"></button>
                    </div>
                </div>
                <div class="row">
                    <div class="cell">
                        <div>:</div>
                    </div>
                    <div class="cell">
                        <input v-model="startLocation" />
                    </div>
                    <div class="cell">
                        <button @click="selectStartLocation"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="workBodyContainer">
            <div class="funContainer">
                <button class="btn marginbottom" @click="sayStation" :disabled="SayStation"></button>
                <button class="btn marginbottom" @click="cancelStation" :disabled="Rest"></button>


                <!--  -->
                <ul class="marginbottom taskList" :class="taskDisplayList">
                    <li>:{{ currentTask.StartLocation }}</li>
                    <li>:{{ currentTask.DestinatLocation }}</li>
                    <li>:{{ currentTask.CarCount }}</li>
                    <li>:{{ currentTask.RealFangliang }}</li>
                    <li>:{{ currentTask.type }}</li>
                </ul>


                <button class="btn" @click="acceptHandler" :disabled="Accept"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="loadThingHandler" :disabled="LoadThing"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="startHandler" :disabled="Start"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="arrivalHandler" :disabled="Arrive"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="startOffHandler" :disabled="StartOff"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="offCompleteHandler" :disabled="OffCompelete"></button>
                <img src="../imgs/downArrow.png" />
                <button class="btn" @click="backHandler" :disabled="Back"></button>
            </div>


        </div>
    </div>
</template>
  
<script>
const PubSub = require('pubsub-js')

export default {
    data() {
        return {
            taskDisplayList: 'noneStyle',
            currentTask: {},
            timer: null,
            UserInfo: {},
            carNumber: '',
            startLocation: '',
            SayStation: false,
            Rest: true,
            Accept: true,
            LoadThing: true,
            Start: true,
            Arrive: true,
            StartOff: true,
            OffCompelete: true,
            Back: true
        }
    },
    name: 'workerPage',
    methods: {
        acceptHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        AcceptTaskTime: this.$getCurrentTime().time,
                        state: 'accept',
                        comment: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.LoadThing = false;
                            clearInterval(this.timer)
                            this.timer = null;
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        loadThingHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        LoadThingTime: this.$getCurrentTime().time,
                        state: 'loadThing',
                        comment: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Start = false;
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        clearState() {
            this.SayStation = true
            this.Rest = true
            this.Accept = true
            this.LoadThing = true
            this.Start = true
            this.Arrive = true
            this.StartOff = true
            this.OffCompelete = true
            this.Back = true
        },
        //
        selectCarNumber() {
            this.$router.push({
                name: 'selectionITemPage',
                query: {
                    type: 'carNumber',
                    currentValue: this.carNumber
                }
            })
        },
        //
        selectStartLocation() {
            this.$router.push({
                name: 'selectionITemPage',
                query: {
                    type: 'startLocation',
                    currentValue: this.startLocation
                }
            })
        },
        //
        handleSelectedValue(msg, data) {
            switch (data.type) {
                case 'carNumber':
                    this.carNumber = data.value;
                    break;
                case 'startLocation':
                    this.startLocation = data.value;
                    break;
            }
        },
        //
        sayStation() {

            if (this.carNumber.trim() === '') {
                alert('')
                return;
            }
            if (this.startLocation.trim() === '') {
                alert('')
                return;
            }


            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber
                },
                body: {
                    $set: {
                        StartLocation: this.startLocation,
                        CarNumber: this.carNumber,
                        UserName: user.UserName,
                        RealName: user.RealName,
                        sayStationTime: this.$getCurrentTime().time,
                        state: 'waitTask',
                        comment: '',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Rest = false;

                            if (this.timer === null) {
                                this.checkTask()
                                this.timer = setInterval(this.checkTask, 2000);
                            }

                            alert('')
                        },
                        fail => {

                        }
                    )

                },
                (fail) => {
                    console.log("");
                }
            );


        },
        //
        cancelStation() {

            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        CarNumber: this.carNumber,
                        StartLocation: '',
                        UserName: '',
                        RealName: '',
                        state: 'rest',
                        comment: '',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.SayStation = false;
                            clearInterval(this.timer)
                            this.timer = null;
                            this.taskDisplayList = 'noneStyle'
                            alert('')
                        },
                        fail => {

                        }
                    )
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        //
        startHandler() {
            let tm=this.$getCurrentTime();
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        leaveStationTime: tm.time,
                        startYear:tm.yy,
                        startMonth:tm.mm,
                        startDay:tm.dd,
                        state: 'start',
                        comment: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("");
                            break;
                        case "body-where-is-null":
                            console.log("");
                            break;
                        case "body-body-is-null":
                            console.log("");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.Arrive = false;
                            break;
                        case "dataBase-error":
                            console.log("");
                            break;
                        case "server-undefinedError":
                            console.log("");
                            break;
                    }
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        //
        arrivalHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        arrivalTime: this.$getCurrentTime().time,
                        state: 'arrival',
                        comment: ','
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("");
                            break;
                        case "body-where-is-null":
                            console.log("");
                            break;
                        case "body-body-is-null":
                            console.log("");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.StartOff = false;
                            break;
                        case "dataBase-error":
                            console.log("");
                            break;
                        case "server-undefinedError":
                            console.log("");
                            break;
                    }
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        //
        startOffHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        startOffTime: this.$getCurrentTime().time,
                        state: 'startOff',
                        comment: ''
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("");
                            break;
                        case "body-where-is-null":
                            console.log("");
                            break;
                        case "body-body-is-null":
                            console.log("");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.OffCompelete = false;
                            break;
                        case "dataBase-error":
                            console.log("");
                            break;
                        case "server-undefinedError":
                            console.log("");
                            break;
                    }
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        //
        offCompleteHandler() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        offCompleteTime: this.$getCurrentTime().time,
                        state: 'offComplete',
                        comment: ','
                    }
                }
            };


            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "body-is-null":
                            console.log("");
                            break;
                        case "body-where-is-null":
                            console.log("");
                            break;
                        case "body-body-is-null":
                            console.log("");
                            break;
                        case "handleSucess":
                            this.clearState();
                            this.Back = false;
                            break;
                        case "dataBase-error":
                            console.log("");
                            break;
                        case "server-undefinedError":
                            console.log("");
                            break;
                    }
                },
                (fail) => {
                    console.log("");
                }
            );
        },
        //
        backHandler() {
            //
            let timeObj = this.$getCurrentTime();
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            let stateInfo = {
                where: {
                    CarNumber: this.carNumber,
                    UserName: user.UserName
                },
                body: {
                    $set: {
                        backTime: timeObj.time,
                        state: 'waitTask',
                        comment: '',
                        DestinatLocation: '',
                        CarCount: '',
                        RealFangliang: '',
                        type: '',
                        LoadLocation: ''
                    }
                }
            };

            //
            this.getCurrentUserCarStatData().then(
                success => {
                    success.backTime = timeObj.time
                    let insertData = {
                        StartLocation: success.StartLocation,
                        CarNumber: success.CarNumber,
                        MaxFangliang: success.MaxFangliang,
                        UserName: success.UserName,
                        RealName: success.RealName,
                        sayStationTime: success.sayStationTime,
                        leaveStationTime: success.leaveStationTime,
                        startYear:success.startYear,
                        startMonth:success.startMonth,
                        startDay:success.startDay,
                        arrivalTime: success.arrivalTime,
                        startOffTime: success.startOffTime,
                        offCompleteTime: success.offCompleteTime,
                        backTime: success.backTime,
                        CarCount: success.CarCount,
                        DestinatLocation: success.DestinatLocation,
                        RealFangliang: success.RealFangliang,
                        type: success.type,
                        LoadLocation: success.LoadLocation,
                    }

                    this.$http.post("/insertTransInfo", insertData).then(
                        (scucess) => {
                            switch (scucess.data) {
                                case "handleSucess":
                                    console.log("");
                                    //
                                    this.updateCarStateInfo(stateInfo)
                                    break;
                                case "body-is-null":
                                    console.log("body-is-null");
                                    break;
                                case "dataBase-error":
                                    console.log("");
                                    break;
                                case "server-undefinedError":
                                    console.log("");
                                    break;
                            }
                        },
                        (fail) => {
                            console.log("");
                        }
                    );


                },
                fail => { alert('!') }
            )



        },
        //
        updateCarStateInfo(stateInfo) {
            this.$http.post("/updateTransStateInfo", stateInfo).then(
                (scucess) => {

                    this.$handleRequest(scucess.data).then(
                        success => {
                            this.clearState();
                            this.Rest = false;
                            this.taskDisplayList = 'noneStyle'
                            if (this.timer === null) {
                                this.checkTask()
                                this.timer = setInterval(this.checkTask, 2000);
                            }
                        },
                        fail => {

                        }
                    )

                },
                (fail) => {
                    console.log("");
                }
            );
        },
        getCurrentUserCarStatData() {
            return new Promise((resolve, rejects) => {
                let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
                //
                let where = {
                    UserName: user.UserName
                };
                //
                this.$http
                    .get(`/transStateQuerys?where=${JSON.stringify(where)}`)
                    .then((info) => {
                        //200
                        this.$handleRequest(info.data).then(
                            success => {
                                console.log("")
                                //
                                let db = success[0];
                                resolve(db);
                            },
                            fail => {
                                rejects();
                                console.log("")
                            }
                        )
                    },
                        (fail) => { console.log(fail) })
                    .catch((err) => {
                        console.log(`${err}`);
                    });
            })
        },
        getCarStateList() {
            let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
            //
            let where = {
                UserName: user.UserName
            };
            //
            this.$http
                .get(`/transStateQuerys?where=${JSON.stringify(where)}`)
                .then((info) => {
                    //200
                    this.$handleRequest(info.data).then(
                        success => {
                            console.log("")
                            //
                            let db = success[0];
                            if (db.state === 'waitAccept') {
                                this.Rest = true;
                                this.Accept = false;
                                clearInterval(this.timer)
                                this.timer = null;
                                this.currentTask = db;
                                this.taskDisplayList = ''
                            }
                        },
                        fail => {
                            this.carStateList = [];
                            console.log("")
                        }
                    )
                },
                    (fail) => { console.log(fail) })
                .catch((err) => {
                    console.log(`${err}`);
                });
        },
        checkTask() {
            this.getCarStateList()
        }
    },
    mounted() {
        console.log('mounted')
        //
        //
        //
        //
        this.getCurrentUserCarStatData().then(
            success => {
                //
                this.clearState();
                this.carNumber = success.CarNumber;
                this.startLocation = success.StartLocation;
                switch (success.state) {
                    case "rest":
                        this.SayStation = false;
                        break;
                    case "waitTask":
                        //
                        this.Rest = false;
                        if (this.timer === null) {
                            this.checkTask()
                            this.timer = setInterval(this.checkTask, 2000);
                        }
                        break;
                    case "waitAccept":
                        this.Accept = false;
                        //
                        this.checkTask()
                        break;
                    case "accept":
                        this.LoadThing = false;
                        break;
                    case "loadThing":
                        this.Start = false;
                        break;
                    case "start":
                        this.Arrive = false;
                        break;
                    case "arrival":
                        this.StartOff = false;
                        break;
                    case "startOff":
                        this.OffCompelete = false;
                        break;
                    case "offComplete":
                        this.Back = false;
                        break;
                    case "back":
                        this.Rest = false;
                        break;
                }
            },
            fail => {
                //
                let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
                this.carNumber = user.CarNumber;
                this.clearState();
                this.SayStation = false;
            })
    },
    created() {
        console.log('created')
        //  
        PubSub.subscribe('selectedValue', this.handleSelectedValue)
    },
    activated() {
        console.log('activated')
        let user = JSON.parse(window.localStorage.getItem(this.$UserInfoKey));
        if (user == null || user == undefined) {
            alert('!')
            return;
        }
        if (user.UserName === this.UserInfo.UserName) {
            //
            if (!this.Rest && this.Accept) {
                //
                if (this.timer === null) {
                    this.checkTask()
                    this.timer = setInterval(this.checkTask, 2000);
                }
            }
            return;
        }
        if (user.UserName != this.UserInfo.UserName) {
            //
            this.UserInfo.UserName = user.UserName;
            this.UserInfo.Password = user.Password;
            this.UserInfo.CarNumber = user.CarNumber;
            this.UserInfo.RealName = user.RealName;
            this.UserInfo.IDNumber = user.IDNumber;

            //

            // 

            return;
        }
    },
    deactivated() {
        console.log('deactivated')
        if (this.timer != null) {
            clearInterval(this.timer)
            this.timer = null;
        }
    }
}

</script>
  
<style>
.marginbottom {
    margin-bottom: 5vw;
}

.workMainWindowRoot .workNavContainer {
    text-align: center;
}

.workMainWindowRoot .workHeaderContainer {
    display: flex;
    justify-content: space-around;
}

.workMainWindowRoot .workHeaderContainer .cell:first-child {
    width: 15vw;
}

.workNavContainer .cell div {
    width: 20vw;
}



.workBodyContainer {
    display: flex;
    justify-content: center;
}

.workBodyContainer .taskList {
    color: red;
}

.workBodyContainer .funContainer {
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 110vh;
}

.workBodyContainer .funContainer button {
    width: 80vw;
    line-height: 10vw;
    border: 1px solid orange;
    display: flex;
    justify-content: center;
    align-items: center;
}

.workBodyContainer .funContainer button:active {
    background-color: orange;
}

.workBodyContainer .funContainer img {
    width: 10vw;
    height: 5vh;
}
</style>
  