<template>
    <div class="carBossCarStateStaticRoot">

        <!--  -->
        <ul class="table">
            <li class="row">
                <div class="cell">{{ sumCount }}</div>
            </li>
            <li class="row">
                <div class="cell">{{ restCount }}</div>
                <div class="cell">{{ waitTaskCount }}</div>
                <div class="cell">{{ waitAcceptCount }}</div>
            </li>
            <li class="row">
                <div class="cell">{{ acceptCount }}</div>
                <div class="cell">{{ loadThingCount }}</div>
                <div class="cell">{{ startCount }}</div>
            </li>
            <li class="row">
                <div class="cell">{{ arrivalCount }}</div>
                <div class="cell">{{ startOffCount }}</div>
                <div class="cell">{{ offCompleteCount }}</div>
            </li>
        </ul>


        <!--  -->
        <ul class="table">
            <li class="row">
                <div class="cell"></div>
                <div class="cell"></div>
                <div class="cell"></div>
                <div class="cell"></div>
                <div class="cell"></div>
                <div class="cell"></div>
                <div class="cell"></div>
            </li>
            <li v-for="(item, i) in carStateList" class="row">
                <div class="cell">{{ item.index }}</div>
                <div class="cell">{{ item.CarNumber }}</div>
                <div class="cell">{{ item.StartLocation }}</div>
                <div class="cell">{{ item.DestinatLocation }}</div>
                <div class="cell">{{ item.RealFangliang }}</div>
                <div class="cell">{{ item.RealName }}</div>
                <div class="cell">{{ item.comment }}</div>
            </li>
        </ul>
    </div>
</template>
  
<script>

export default {
    name: 'carBossCarStateStatic',
    methods: {
        refresh() {
            this.getCarStateList();
        },
        getCarStateList() {
            //
            let where = {
            };
            //
            this.$http
                .get(`/transStateQuerys?where=${JSON.stringify(where)}`)
                .then((info) => {
                    //200
                    this.$handleRequest(info.data).then(
                        success => {
                            console.log("")
                            let carStateIndex = 0;
                            this.carStateList = [];
                            this.sumCount = success.length;
                            let rest = 0;
                            let waitTask = 0;
                            let waitAccept = 0;
                            let accept = 0;
                            let loadThing = 0;
                            let start = 0;
                            let arrival = 0;
                            let startOff = 0;
                            let offComplete = 0;

                            success.forEach(success => {
                                carStateIndex++
                                success.index = carStateIndex
                                this.carStateList.push(success)

                                switch (success.state) {
                                    case "rest":
                                        rest++;
                                        break;
                                    case "waitTask":
                                        waitTask++
                                        break;
                                    case "waitAccept":
                                        waitAccept++
                                        break;
                                    case "accept":
                                        accept++
                                        break;
                                    case "loadThing":
                                        loadThing++
                                        break;
                                    case "start":
                                        start++
                                        break;
                                    case "arrival":
                                        arrival++
                                        break;
                                    case "startOff":
                                        startOff++
                                        break;
                                    case "offComplete":
                                        offComplete++
                                        break;
                                }

                            });

                            this.restCount = rest;
                            this.waitTaskCount = waitTask;
                            this.waitAcceptCount = waitAccept;
                            this.acceptCount = accept;
                            this.loadThingCount = loadThing;
                            this.startCount = start;
                            this.arrivalCount = arrival;
                            this.startOffCount = startOff;
                            this.offCompleteCount = offComplete;
                        },
                        fail => {
                            this.carStateList = [];
                            console.log("")
                        }
                    )
                },
                    (fail) => { console.log(fail) })
                .catch((err) => {
                    console.log(`${err}`);
                });
        }
    },
    data() {
        return {
            sumCount: 0,
            restCount: 0,
            waitTaskCount: 0,
            waitAcceptCount: 0,
            acceptCount: 0,
            loadThingCount: 0,
            startCount: 0,
            arrivalCount: 0,
            startOffCount: 0,
            offCompleteCount: 0,
            timer: null,
            carStateList: []
        }
    },
    created() {
        this.refresh()
        this.timer = setInterval(this.refresh, 2000);
    },
    beforeDestroy() {
        clearInterval(this.timer)
        this.timer = null;
    }
}
</script>
  
<style scoped>
.carBossCarStateStaticRoot .table {
    width: 100vw;
}

.carBossCarStateStaticRoot .table .row {
    justify-content: center;
}


.carBossCarStateStaticRoot .table:nth-child(2) .cell {
    flex: 1;
    justify-content: center;
    align-items: center;
}




/* .carBossCarStateStaticRoot .table:first-child .row>div {
    background-color: orange;
} */
.carBossCarStateStaticRoot .table:first-child {
    border-bottom: 1px solid black;
}

.carBossCarStateStaticRoot .table:first-child .row {
    display: flex;
    justify-content: flex-start;
}

.carBossCarStateStaticRoot .table:first-child .row .cell {
    flex: 1;
}
</style>
  