<template>
  <div class="logInRoot">
    <div class="title">
      <img src="../imgs/loginTitle.jpg" />
    </div>
    <div class="tableContainer">
      <div class="table">
        <div class="row">
          <div class="cell">
            <span>:</span>
          </div>
          <div class="cell">
            <input v-model="userName" />
          </div>
        </div>
        <div class="row">
          <div class="cell">
            <span>&#160;&#160;&#160;:</span>
          </div>
          <div class="cell">
            <input v-model="password" />
          </div>
        </div>
        <div class="row">
          <div class="cellSpatial">
            <button @click="login"></button>
          </div>
          <div class="cellSpatial">
            <button @click="register"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "logIn",
  data() {
    return {
      userName: "duizhang",
      password: "123",
    };
  },
  components: {},
  methods: {
    register() {
      this.$router.push({ name: "register" });
    },
    handleRequest(data) {
      switch (data) {
        case "where-undefined":
          //whereundefined
          console.log("where-undefined  whereundefined");
          break;
        case "data-null":
        window.alert('')
          //where
          console.log("data-null where");
          break;
        case "dataBase-error":
          //
          console.log("dataBase-error ");
          break;
        case "server-undefinedError":
          //
          console.log("server-undefinedError ");
          break;
        default:
          //
          //window.alert('')
          this.$router.push({ name: "driverMainWindow" });
          window.localStorage.setItem(
            this.$UserInfoKey,
            JSON.stringify(data)
          );
          break;
      }
    },
    login() {
      if (this.userName === "duizhang" && this.password === "123") {
        this.$router.push({ name: "carBossMainWindow" });
      }else if(this.userName === "jianhua" && this.password === "123"){
        this.$router.push({ name: "dispatcherWorkPage" });
      }else {
        //
        let where = {
          UserName: this.userName,
          Password: this.password,
        };
        //
        this.$http
          .get(`/userInfo?where=${JSON.stringify(where)}`)
          .then((info) => {
            //200
            this.handleRequest(info.data);
          },
          (fail)=>{
            console.log(fail)
          })
          .catch((err) => {
            console.log(`${err}`);
          });
      }
    },
  },
};
</script>
  
<style>
.logInRoot {
  height: 100vh;
  font-weight: 700;
  font-size: 4vw;
  /* background-image: url('../imgs/login.jpg');
    background-repeat: no-repeat;
    background-size: cover; */
}

.logInRoot img {
  width: 43vw;
}

.logInRoot .title {
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.logInRoot .tableContainer {
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.tableContainer .cell:first-child {
  width: 16vw;
}

.tableContainer .table .row:last-child {
  justify-content: space-around;
}

.tableContainer .table .row:last-child .cellSpatial {
  flex: 1;
}

.tableContainer .table .row:last-child .cellSpatial button {
  width: 30vw;
  height: 30px;
  margin: 0vw 3vw;
  background-color: red;
}
</style>
  