    background-color: rgba(255, 255, 255, 0.9);
    border-right: 1px solid black;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.noneStyle {
    display: none;
}

.taskContent input {
    width: 100px;
    border: 1px solid black;
}

.taskContent .tablePC {
    width: 200px;
    display: flex;
    justify-content: flex-start;
    height: 150px;
}

.taskContent .tablePC .rowPC {
    display: flex;
    flex-direction: row;
    height: 1.5em;
    margin-left: 10px;
}

.taskContent .tablePC .cellPC:first-child {
    width: 70px;
}

.taskContent .tablePC .cellPC:nth-child(2) {
    width: 100px;
}


.taskContent button {
    height: 25px;
    width: 177px;
    font-size: 13px;
}


/*  */
.loadThingList {
    display: flex;
    flex-direction: row;
}

.loadThingList .sayStationList {
    width: 50%;
}


/*  */
/* .bottomContainer .carStateList{
    background-color: red;
} */

.carStateList .tablePC {
    border: 1px solid black;
    height: 50vh;
    margin: 5px;
    margin-top: 0;
}

.carStateList .tablePC .rowPC .cellPC {
    width: 100px;
    justify-content: center;
    align-items: center;
}
</style>
    